
import { createProgram } from "./shader-program";
import Keyboard from "./keyboard";
import { gl, initWebGLContext } from "./webgl-context";
import { loadVertexBuffers } from "./vertex-buffers";
import { createTexture } from "./texture";
import { mat4, vec3 } from "gl-matrix";
import { b2CircleShape, b2FixtureDef, b2World, b2BodyType, b2Vec2, b2PolygonShape } from "@box2d/core";

let defaultProgram: WebGLProgram | null;
let texture: WebGLTexture | null;
let assetsUMvpMatrixLocation: WebGLUniformLocation | null;
let objects: any;
const keyboard = new Keyboard();

let lightlessProgram: WebGLProgram | null;

const modelMatrix = mat4.create();
const viewMatrix = mat4.create();
const projMatrix = mat4.create();
const projViewMatrix = mat4.create();
const mvpMatrix = mat4.create();

mat4.ortho(projMatrix, 0, 500, 340, 0, 50, 0);
mat4.lookAt(viewMatrix, [0, 0, 40], [0, 0, 0], [0, 1, 0]);
mat4.mul(projViewMatrix, projMatrix, viewMatrix);

enum objIndex
{
    background = 0,
    player = 6,
    static = 12,
    hole = 18
}

const worldScale = 30;
const world = b2World.Create({ x: 0, y: 9.8 });

const playerShape = new b2CircleShape(10 / worldScale);
const playerFixtureDef: b2FixtureDef = { shape: playerShape, friction: 1, density: 2 };
const playerBody = world.CreateBody();
playerBody.CreateFixture(playerFixtureDef);
playerBody.SetType(b2BodyType.b2_dynamicBody);
playerBody.SetTransformVec({ x: 250 / worldScale, y: 175 / worldScale }, 0);
playerBody.SetSleepingAllowed(false);
// playerBody.setMassData({
//     mass: 1,
//     center: planck.Vec2(), I: 1
// });
playerBody.SetFixedRotation(true);

// 2. Force
// let remainingJumpSteps = 0;

let assetsVertPosBuffer: WebGLBuffer | null;
let assetsTexCoordBuffer: WebGLBuffer | null;

document.body.onkeydown = (event) =>
{
    if (!event.repeat)
    {
        if (event.key === "w" || event.key == " ")
        {
            // 1. Velocity directly
            // playerBody.getLinearVelocity().y = -5;

            // 2. Force
            // remainingJumpSteps = 6;

            // 3. Impulse
            const impulse = playerBody.GetMass() * -8.5;
            playerBody.ApplyLinearImpulse(new b2Vec2(0, impulse), playerBody.GetWorldCenter());
        }
    }
};

function gameLoop()
{
    requestAnimationFrame(gameLoop);

    gl.useProgram(defaultProgram);

    // Moving
    if (keyboard.pressed("a") || keyboard.pressed("ArrowLeft"))
    {
        const vel = playerBody.GetLinearVelocity();
        vel.Set(-5, vel.y);
    }
    if (keyboard.pressed("d") || keyboard.pressed("ArrowRight"))
    {
        const vel = playerBody.GetLinearVelocity();
        vel.Set(5, vel.y);
    }

    // 2. Force
    // if (remainingJumpSteps > 0)
    // {
    //     playerBody.applyForce(planck.Vec2(0, -80), playerBody.getWorldCenter());
    //     remainingJumpSteps--;
    // }

    world.Step(1 / 60, { velocityIterations: 6, positionIterations: 2 });

    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
    gl.bindTexture(gl.TEXTURE_2D, texture);

    // Background
    mat4.identity(modelMatrix);
    mat4.translate(modelMatrix, modelMatrix, vec3.fromValues(0, 0, 0));
    mat4.scale(modelMatrix, modelMatrix, vec3.fromValues(500, 350, 1));
    mat4.mul(mvpMatrix, projViewMatrix, modelMatrix);
    gl.uniformMatrix4fv(assetsUMvpMatrixLocation, false, mvpMatrix);
    gl.drawArrays(gl.TRIANGLES, objIndex.background, 6);

    // Player
    mat4.identity(modelMatrix);
    mat4.translate(modelMatrix, modelMatrix,
        vec3.fromValues(playerBody.GetPosition().x * worldScale,
            playerBody.GetPosition().y * worldScale, -10));
    // console.log(playerBody.getAngle());
    mat4.scale(modelMatrix, modelMatrix, vec3.fromValues(20, 20, 1));
    mat4.mul(mvpMatrix, projViewMatrix, modelMatrix);
    gl.uniformMatrix4fv(assetsUMvpMatrixLocation, false, mvpMatrix);
    gl.drawArrays(gl.TRIANGLES, objIndex.player, 6);
    // Static
    for (let i = 0; i < objects.length; i++)
    {
        if (objects[i].name === "Static")
        {
            mat4.identity(modelMatrix);
            mat4.translate(modelMatrix, modelMatrix, vec3.fromValues(objects[i].x, objects[i].y, -10));
            mat4.scale(modelMatrix, modelMatrix, vec3.fromValues(objects[i].width, objects[i].height, 1));
            mat4.mul(mvpMatrix, projViewMatrix, modelMatrix);
            gl.uniformMatrix4fv(assetsUMvpMatrixLocation, false, mvpMatrix);
            gl.drawArrays(gl.TRIANGLES, objIndex.static, 6);
        }
        else if (objects[i].name === "Hole")
        {
            mat4.identity(modelMatrix);
            mat4.translate(modelMatrix, modelMatrix, vec3.fromValues(objects[i].x, objects[i].y, -10));
            mat4.scale(modelMatrix, modelMatrix, vec3.fromValues(objects[i].width, objects[i].height, 1));
            mat4.mul(mvpMatrix, projViewMatrix, modelMatrix);
            gl.uniformMatrix4fv(assetsUMvpMatrixLocation, false, mvpMatrix);
            gl.drawArrays(gl.TRIANGLES, objIndex.hole, 6);
        }
    }
}

async function init()
{
    if (!initWebGLContext("renderCanvas")) return;

    gl.enable(gl.DEPTH_TEST);
    gl.enable(gl.BLEND);
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);
    gl.viewport(0, 0, gl.canvas.clientWidth, gl.canvas.clientHeight);
    gl.clearColor(0.2, 0.3, 0.4, 1.0);

    let response = await fetch("assets/shaders/default.vert");
    const defaultVertShaderSrc = await response.text();
    response = await fetch("assets/shaders/default.frag");
    const defaultFragShaderSrc = await response.text();
    defaultProgram = createProgram(defaultVertShaderSrc, defaultFragShaderSrc);
    if (!defaultProgram) return;

    assetsUMvpMatrixLocation = gl.getUniformLocation(defaultProgram, "uMvpMatrix");

    assetsVertPosBuffer = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, assetsVertPosBuffer);
    const assetsAPositionLocation = gl.getAttribLocation(defaultProgram, "aPosition");
    gl.vertexAttribPointer(assetsAPositionLocation, 3, gl.FLOAT, false, 0, 0);
    gl.enableVertexAttribArray(assetsAPositionLocation);

    assetsTexCoordBuffer = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, assetsTexCoordBuffer);
    const assetsATexCoordLocation = gl.getAttribLocation(defaultProgram, "aTexCoord");
    gl.vertexAttribPointer(assetsATexCoordLocation, 2, gl.FLOAT, false, 0, 0);
    gl.enableVertexAttribArray(assetsATexCoordLocation);

    const assetsImage = document.getElementById("assetsImage") as HTMLImageElement;
    texture = createTexture(assetsImage);

    response = await fetch("assets/sprites/texture.json");
    const assetsJson = await response.json();
    loadVertexBuffers(defaultProgram, assetsJson, assetsVertPosBuffer, assetsTexCoordBuffer);

    response = await fetch("assets/shaders/lightless.vert");
    const lightlessVertShaderSrc = await response.text();
    response = await fetch("assets/shaders/lightless.frag");
    const lightlessFragShaderSrc = await response.text();
    lightlessProgram = createProgram(lightlessVertShaderSrc, lightlessFragShaderSrc);
    if (!lightlessProgram) return;



    response = await fetch("assets/levels/level00.json");
    const level00Json = await response.json();
    const layers = level00Json.layers;
    for (let i = 0; i < layers.length; i++)
    {
        if (layers[i].name == "Object Layer 1")
        {
            objects = layers[i].objects;

            for (let i = 0; i < objects.length; i++)
            {
                if (objects[i].name === "Static")
                {
                    const x = objects[i].x;
                    const y = objects[i].y;
                    const w = objects[i].width;
                    const h = objects[i].height;

                    const shape = new b2PolygonShape();
                    shape.SetAsBox(w / 2 / worldScale, h / 2 / worldScale);
                    const fixtureDef: b2FixtureDef = { shape: shape, friction: 1 };
                    const body = world.CreateBody();
                    body.CreateFixture(fixtureDef);
                    body.SetType(b2BodyType.b2_staticBody);
                    body.SetTransformVec({
                        x: (x + w / 2) / worldScale, y: (y + h / 2) / worldScale
                    }, 0);
                }
                // else if (objects[i].name === "Hole")
                // {
                // }
            }
        }
    }
    gameLoop();
}

init();
